"use scrict";
import {isEmpty} from "@/utils/util.js";

export default {
	data() {
		return {
			isAll: false,
			blist: []
		}
	},
	model: {
		prop: "value",
		event: "change"
	},
	props: {
		value: {
			type: Array,
			default() {
				return [];
			}
		},
		list: {
			type: Array,
			default() {
				return [];
			}
		},
		itemValue: {
			type: String,
			default: "value"
		},
		children: {
			type: String,
			default: "children"
		},
		async: {
			type: Function,
			default: null
		}
	},
	watch: {
		list(value) {
			this.blist = value.map(item => {
				item.checked = false;
				item.showChildren = false;
				Array.isArray(item[this.children]) && item[this.children].forEach(child => child.checked = false);
				return item;
			});
		}
	},
	methods: {
		selectAll: function (value) {
			this.isAll = value;
			this.isAll ? this._setAll(true) : this._setAll(false);
			this.$emit("change", this._getValue());
		},
		selectParent: async function (e, {item, i}) {
			// 异步获取子数组
			this.async && !item[this.children] && (item[this.children] = await this.async({item, index: i}));
			// 判断是否显示子项
			const tab = e.target.dataset.tab;
			tab === "box" && (item.showChildren = !item.showChildren);
			// 同步项
			tab === "checkbox" && (item.checked = !item.checked);
			// 子项于该项选择一致
			Array.isArray(item[this.children]) && item[this.children].forEach(child => child.checked = item.checked);
			// 渲染该项
			this.$set(this.blist, i, item);
			// 是否全选
			this.isAll = this.blist.every(item => item.checked);
			// 提交数据
			this.$emit("change", this._getValue());
		},
		selectChild: function (e, {item, child, i, j}) {
			const tab = e.target.dataset.tab;
			tab === "checkbox" && (child.checked = !child.checked);
			tab === "child-box" && (child.showChildren = !child.showChildren);
			Array.isArray(child[this.children]) && child[this.children].forEach(son => son.checked = child.checked);
			item.checked = item[this.children].every(child => child.checked === true);
			this.$set(this.blist, i, item);
			this.$emit("change", this._getValue());
		},
		selectSon: function (e, {child, son, i, j, k}) {
			son.checked = !son.checked;
			child.checked = child[this.children].every(son => son.checked === true);
			this.$set(this.blist[i][this.children], j, child);
			this.$emit("change", this._getValue());
		},
		_setAll: function (value, list = this.blist) {
			list.forEach(item => {
				item.checked = value;
				item.children && item.children.length && this._setAll(value, item.children);
			});
			this.blist = list;
		},
		_getValue: function (result = [], list = this.blist) {
			list.forEach(item => {
				if (item.children && item.children.length) {
					this._getValue(result, item.children);
				} else {
					item.checked === true && result.push(item);
				}
			});
			return result;
		}
	}
}
